import {Directive, Input} from '@angular/core';
import {AdaptChartAbstract, AdaptChartAxis} from './chart.model';

@Directive()
export abstract class AdaptHeatmapAbstract<T> extends AdaptChartAbstract {
  @Input() data: AdaptHeatmapData<T>;
  @Input() xAxis: AdaptChartAxis;
  @Input() yAxis: AdaptChartAxis;
}

export interface AdaptHeatmapData<T> {
  reverse?: boolean;
  items: AdaptHeatmapNode<T>[][];
}

export interface AdaptHeatmapActionsMenu<T> {
  target: SVGElement;
  event: MouseEvent;
  actions: AdaptHeatmapNodeAction<T>[];
  context: T;
}

export interface AdaptHeatmapNodeAction<T> {
  title: string;
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  callback: (target: SVGElement, event: MouseEvent, context: T) => any;
}

export interface AdaptHeatmapNode<T> {
  name: string;
  size: number;
  status: {
    name: string;
    bg: string;
    color?: string;
    icon?: string;
    iconColor?: string;
  };
  info?: string;
  progress?: number;
  content?: T;
  actions?: AdaptHeatmapNodeAction<T>[];
}
