import {Directive, Input} from '@angular/core';
import {AdaptChartAbstract, AdaptChartAxis, AdaptChartDataModel, AdaptChartSeries} from './chart.model';

@Directive()
export abstract class AdaptLineGraphAbstract<T> extends AdaptChartAbstract {
  @Input() xAxis?: AdaptChartAxis;
  @Input() yAxis?: AdaptChartAxis;
  @Input() abstract series: AdaptChartSeries<T>[];
  @Input() hideAxises?: boolean;
}

export interface AdaptLineGraphDateAndData {
  date: string;
  data: number;
}

export interface AdaptLineGraphData extends AdaptChartDataModel<AdaptLineGraphDateAndData> {
  hidden: boolean;
}
