import {Directive, EventEmitter, Input, Output, TemplateRef} from '@angular/core';
import * as d3 from 'd3';

// TODO update types
export interface AdaptChartTooltip {
  color: string;
  category: string;
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  xValue: any;
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  yValue: any;
}

@Directive()
export abstract class AdaptChartAbstract {
  @Input() backgroundColor: string;
  @Input() width: number;
  @Input() height: number;
  @Input() header: AdaptChartHeader;
  @Input() abstract legend: AdaptChartLegend;
  @Input() suppressHeader: boolean;
  @Input() suppressLegend: boolean;
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() tooltip?: string | TemplateRef<any>;
  @Input() dataBuilder: (g: AdaptSVGGroupElement, dim: AdaptChartDimensions) => void;
  @Input() noDataText?: string;

  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Output() readonly clickChart: EventEmitter<any>;
}

export interface AdaptChartLegend {
  columns?: number;
  items: string[] | AdaptChartLegendState[];
  customColors?: string[];
  activeHover?: boolean;
  activeClick?: boolean;
  width?: number;
}

// @since 10.8.0
export interface AdaptChartGradientLegend {
  increaseColor: string;
  decreaseColor: string;
  zeroColor?: string;
  increaseText?: string;
  decreaseText?: string;
  zeroText?: string;
  height?: string;
}

export interface AdaptChartLegendState {
  hidden: boolean;
  hover: boolean;
  item: string;
}

export interface AdaptChartCategoryToggleEvent {
  category: string;
}

export interface AdaptChartCategoryEvent {
  id: number;
  event: MouseEvent;
}

export interface AdaptChartAxis {
  title?: string;
  thickness?: number;
  categories?: string[];
}

export interface AdaptChartDimensions {
  w: number;
  h: number;
}

export interface AdaptChartHeader {
  title?: string;
  subtitle?: string;
  allowHideLegend?: boolean;
}

export interface AdaptChartDataModel<T> {
  name: string;
  data: T[];
}

export interface AdaptChartSeries<T> {
  name?: string;
  hidden?: boolean;
  data: T[];
}

export interface AdaptChartBoundaries {
  top: number;
  right: number;
  bottom: number;
  left: number;
}

export type AdaptChartSeriesFormat = 'vertical' | 'horizontal';

/* eslint-disable-next-line @typescript-eslint/no-explicit-any */
export type AdaptSVGGroupElement = d3.Selection<SVGGElement, any, any, any>;

export enum AdaptChartSliderType {
  bar = 'bar',
  line = 'line',
  scatterplot = 'scatterplot'
}
