import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  HostBinding,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
  ViewEncapsulation
} from '@angular/core';
import {DomSanitizer, SafeHtml} from '@angular/platform-browser';
import * as shape from 'd3-shape';
import {Subject} from 'rxjs';

import {
  AdaptDeviceDetectionService,
  AdaptIconConfig,
  adaptWarn,
  debounce,
  SafeCdr,
  toBoolean
} from '@bmc-ux/adapt-angular';
import {AdaptSvgIconName} from '@bmc-ux/dpl-iconfont';

import {
  AdaptFlowChartCustomTemplate,
  AdaptFlowChartEdge,
  AdaptFlowChartEmptyStateConfig,
  AdaptFlowChartNode,
  AdaptFlowChartOrientation,
  AdaptFlowChartTexts
} from '../common/models/flow-chart.model';
import {ChartColorService, AdaptChartColorStatus} from '../common/services/chart-color.service';

@Component({
  selector: 'adapt-flow-chart',
  templateUrl: './flow-chart.component.html',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AdaptFlowChartComponent implements OnInit, OnChanges {
  center$: Subject<boolean> = new Subject();
  zoomToFit$: Subject<boolean> = new Subject();
  panToNode$: Subject<string> = new Subject();
  update$: Subject<boolean> = new Subject();

  mainTextLeftPadding: number = 70;
  mainTextTopPadding: number = 34;
  mainTextTopGap: number = 6;

  mainIconDimension: number = 40;
  mainIconLeftPadding: number = 15;
  mainIconTopPadding: number = 20;

  defaultNodeHeight: number = 78;
  defaultNodeWidth: number = 280;

  nodeRoundRadix: number = 45;
  additionalInfoRowHeight: number = 42;
  additionalIconDimension: number = 13;
  basicLeftPadding: number = 12;
  additionalLeftPadding: number = 4;
  additionalIconTopPadding: number = 93;
  additionalTextTopPadding: number = 103;
  additionalLineTopPadding: number = 120;

  defaultColor: string = 'currentcolor';
  defaultLabelColor: string = 'var(--color-active)';
  defaultBackgroundColor: string = '#fff';
  strokeLineLength: number = 4;

  layoutSettings: { orientation: AdaptFlowChartOrientation } = {
    orientation: AdaptFlowChartOrientation.BOTTOM_TO_TOP
  };

  zoomLevel: number;
  minZoomLevel: number = 0.1;
  maxZoomLevel: number = 4;
  handleZoomChangeDebounced: Function = debounce(this.handleZoomChange.bind(this), 200);

  loading: boolean = true;
  isIE: boolean = false;
  componentLevelId: string;
  nodeHoverId: string = '';

  @Input() links: AdaptFlowChartEdge[];
  @Input() nodes: AdaptFlowChartNode[];
  @Input() curve: shape.CurveFactory = shape.curveCatmullRom;
  @Input() orientation: AdaptFlowChartOrientation = AdaptFlowChartOrientation.BOTTOM_TO_TOP;

  @Input() zoomSpeed: number = 0.04;
  // @Input() draggingEnabled = false;
  @Input() enableZoom: boolean = true;
  @Input() autoZoom: boolean = false;
  @Input() autoCenter: boolean = false;
  @Input() texts: AdaptFlowChartTexts;
  @Input() autoCenterToNode: string;
  @Input() hasEmptyState: boolean = true;
  @Input() emptyStateConfig: AdaptFlowChartEmptyStateConfig;

  /**
   * Allows to use a custom template for chart elements. Can be node, link or defs templates
   * @since v12.24.0
   * @docs-default
   */
  @Input() customTemplateConfig: AdaptFlowChartCustomTemplate;

  @Output() readonly clickNode: EventEmitter<{ event: Event; node: AdaptFlowChartNode }>
    = new EventEmitter<{ event: Event; node: AdaptFlowChartNode }>();

  @HostBinding('class') class: string = 'adapt-flow-chart';
  @HostBinding('style.background-color') @Input() backgroundColor: string;

  private _defaultTexts: AdaptFlowChartTexts = {
    fitGraphButton: 'Fit to screen'
  };

  private _defaultEmptyStateConfig: AdaptFlowChartEmptyStateConfig = {
    label: '',
    inverted: false
  };

  constructor(private _deviceDetection: AdaptDeviceDetectionService,
              private _changeDetectorRef: ChangeDetectorRef,
              private _domSanitizer: DomSanitizer,
              private _iconConfig: AdaptIconConfig) {
  }

  ngOnInit(): void {
    this.componentLevelId = Math.floor(Math.random() * 1000000).toString();
    this.isIE = this._deviceDetection.isIE();
    this.texts = {...this._defaultTexts, ...this.texts};
    this.emptyStateConfig = {...this._defaultEmptyStateConfig, ...this.emptyStateConfig};
    this.layoutSettings.orientation = this.orientation;
  }

  ngOnChanges(changes: SimpleChanges): void {
    const nodes = changes.nodes;

    if (this.nodes && nodes && nodes.previousValue !== nodes.currentValue) {
      this.nodes.forEach(node => {
        node.dimension = {
          width: node.dimension && node.dimension.width || this.defaultNodeWidth,
          height: this.getNodeHeight(node)
        };
      });

      if (this.autoCenterToNode) {
        if (this.autoCenter) {
          this.autoCenter = false;
          adaptWarn(`ADAPT-ANGULAR Warning! Flow chart component!
          Seems like "autoCenter" is enabled with "autoCenterToNode".
          In this case "autoCenter" will be disabled for correct work of "autoCenterToNode"`);
        }
        setTimeout(() => {
          this.panToNode$.next(this.autoCenterToNode);
          this.autoCenterToNode = null;
        });
      }

      this.loading = false;
    }
  }

  showEmptyState(): boolean {
    return this.hasEmptyState && !this.loading && (!this.nodes || this.nodes.length === 0);
  }

  showGraph(): boolean {
    return toBoolean(!this.loading && this.nodes && this.nodes.length !== 0);
  }

  getStatusColor(status: AdaptChartColorStatus): string {
    return status || status === 0 ? ChartColorService.getColorRange(status, 1)[0] : this.defaultColor;
  }

  getSvgIcon(name: string): SafeHtml {
    return this._domSanitizer.bypassSecurityTrustHtml(this._iconConfig.getIcon(name as AdaptSvgIconName));
  }

  fitGraph(): void {
    this.zoomToFit$.next(true);
    // At the same time centring graph for better user experience
    this.center$.next(true);
  }

  handleNodeClick(event: Event, node: AdaptFlowChartNode): void {
    this.clickNode.emit({event, node});
  }

  handleMouseOver(node: AdaptFlowChartNode): void {
    this.nodeHoverId = node.id;
  }

  handleZoomMouseDown(): void {
    this._disableAutoZoom();
  }

  handleZoomChange(zoomLevel: number): void {
    this._disableAutoZoom();
    this.zoomLevel = zoomLevel;

    SafeCdr.markForCheck(this._changeDetectorRef);
  }

  getNodeHeight(node: AdaptFlowChartNode): number {
    if (node.data && node.data.length) {
      return this.defaultNodeHeight + node.data.length * this.additionalInfoRowHeight;
    } else {
      return node.dimension && node.dimension.height || this.defaultNodeHeight;
    }
  }

  trackByForNodeData(index: number): number {
    return index;
  }

  sliderValueFormatter(value: number): string {
    if ((value ^ 0) === value) {
      return value.toString();
    } else {
      const digitsAfterComma: number = value.toString().split('.')[1].length;
      const roundTo: number = digitsAfterComma > 2 ? 2 : 1;

      return value.toFixed(roundTo);
    }
  }

  private _disableAutoZoom(): void {
    // This hack need to be done to allow zooming with slider if autoZoom feature was enabled by default
    if (this.autoZoom) {
      this.autoZoom = false;
    }

    // Also need to disable autoCenter to avoid centring on zooming
    if (this.autoCenter) {
      this.autoCenter = false;
    }
  }
}
