import {Directive, Input} from '@angular/core';
import {AdaptChartAbstract, AdaptChartAxis, AdaptChartSeries} from './chart.model';

@Directive()
export abstract class AdaptScatterPlotAbstract<T> extends AdaptChartAbstract {
  @Input() xAxis?: AdaptChartAxis;
  @Input() yAxis?: AdaptChartAxis;
  @Input() abstract series: AdaptChartSeries<T>[];
  @Input() hideAxises?: boolean;
}

export interface AdaptScatterPlotData {
  name: string;
  hidden?: boolean;
  color?: string;
  data: number[][];
}
