import {AfterViewInit, Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation} from '@angular/core';
import {Point} from '@angular/cdk/drag-drop';
import {asapScheduler, timer} from 'rxjs';
import {takeUntil} from 'rxjs/operators';
import BigNumber from 'bignumber.js';
import * as d3 from 'd3';
import {inRange} from 'lodash-es';
import {CutNumberPipe} from '../common/pipes/cut-number.pipe';
import {ColorType} from '@bmc-ux/adapt-angular';
import {AdaptLineGraphComponent} from '../line-graph/line-graph.component';
import {AdaptAreaGraphData, AdaptAreaGraphSeries} from '../common/models/area-graph.model';
import {AdaptChartCategoryToggleEvent, AdaptChartDimensions, AdaptChartTooltip} from '../common/models/chart.model';
import {AdaptLineGraphDateAndData} from '../common/models/line-graph.model';

let chartCounter = 1;

@Component({
  selector: 'adapt-area-graph',
  templateUrl: '../line-graph/line-graph.component.html',
  providers: [CutNumberPipe],
  encapsulation: ViewEncapsulation.None
})
export class AdaptAreaGraphComponent extends AdaptLineGraphComponent implements OnInit, AfterViewInit {
  @Input() formatXAxisValue: (domainValue: d3.AxisDomain, index: number) => string;

  protected _data: AdaptAreaGraphData[] = [];
  protected _series: AdaptAreaGraphSeries[] = [];
  protected circleRadius: number = 4;
  protected _id: string;
  private areaOpacity: number = 0.25;
  private areaOpacityForHovered: number = 0.75;

  /**
   * Output that emits each time chart category shown
   * @since 12.11.0
   */
  @Output() readonly categoryShow: EventEmitter<AdaptChartCategoryToggleEvent> = new EventEmitter<AdaptChartCategoryToggleEvent>();

  /**
   * Output that emits each time chart category hidden
   * @since 12.11.0
   */
  @Output() readonly categoryHide: EventEmitter<AdaptChartCategoryToggleEvent> = new EventEmitter<AdaptChartCategoryToggleEvent>();

  @Input() getTooltipXValueTitle(): string {
    return 'x';
  }

  ngOnInit(): void {
    super.ngOnInit();

    this._id = `area-graph-${chartCounter++}`;
  }

  ngAfterViewInit(): void {
    // The change consists of two line graphs (one is the main, second in the slider),
    // so need to check the chart view after they both have been drawn
    timer(0, asapScheduler)
      .pipe(takeUntil(this._destroy$))
      .subscribe(() => {
        this._detectViewChanges();
      });
  }

  getXMaxValue(data: AdaptAreaGraphData[]): number {
    let maxYVal: number = data[0].xyPointData[0].x;

    data.forEach(dataItem => {

      // assume that last value is the biggest value
      const value = dataItem.xyPointData[dataItem.xyPointData.length - 1].x;

      if (value > maxYVal) {
        maxYVal = value;
      }
    });

    return maxYVal;
  }

  getXMinValue(data: AdaptAreaGraphData[]): number {
    let minYVal: number = data[0].xyPointData[0].x;

    data.forEach(dataItem => {

      // assume that first value is the lowest value
      const value = dataItem.xyPointData[0].x;

      if (value < minYVal) {
        minYVal = value;
      }
    });

    return minYVal;
  }

  protected getCircleColor(): string {
    return 'transparent';
  }

  protected getLineRenderer(): d3.Line<Point> {
    return d3.line<Point>()
      .x((d) => this._xPos(d.x))
      .y((d) => this._y(d.y));
  }

  protected getXPosFunc(dim: AdaptChartDimensions, domain: (number | string)[] = []): d3.ScaleLinear<number, number> {
    const [min, max] = domain as number[];

    return d3.scaleLinear()
      .range([0, dim.w - (this._margin.left + this._margin.right)])
      .domain([min || this.getXMinValue(this._data), max || this.getXMaxValue(this._data)]);
  }

  protected getYPosFunc(dim: AdaptChartDimensions, ticksVal: number[]): d3.ScaleLinear<number, number> {
    return d3.scaleLinear()
      .range([0, dim.h - this._margin.bottom - this._margin.top])
      .domain([ticksVal[0], 0]);
  }

  protected getXAxisLineRenderer(dim: AdaptChartDimensions): d3.Axis<d3.AxisDomain> {
    const baseRenderer = super.getXAxisLineRenderer(dim);

    const cats = [...this.xAxis.categories];

    if (cats && cats.length) {
      // for proper rendering with categories
      const maxX = this.getXMaxValue(this._data);
      const minX = this.getXMinValue(this._data);
      const minMaxRange = maxX - minX;
      const tickStep = minMaxRange / (cats.length - 1);

      let tickValuesArray = cats.map((category, index) => minX + tickStep * index);

      if (this.showSlider && this.sliderDomainData?.length) {
        const [min, max] = this.sliderDomainData;

        tickValuesArray = tickValuesArray.filter((tick, index) => {
          if (inRange(tick, min, max)) {
            return true;
          }

          cats[index] = null;
        });
      }

      return baseRenderer
        .tickValues(tickValuesArray)
        .tickFormat((val, ind) => cats.filter(category => !!category)[ind]);
    }

    if (this.showSlider && this.slidePointsCount) {
      baseRenderer.ticks(this.slidePointsCount);
    }

    if (this.formatXAxisValue) {
      return baseRenderer.tickFormat(this.formatXAxisValue);
    }

    return baseRenderer;
  }

  protected mapDataFromSeries(series: AdaptAreaGraphSeries[]): AdaptAreaGraphData[] {
    return series.map((item): AdaptAreaGraphData => {
      return {
        name: item.name,
        hidden: item.hidden,
        /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
        data: item.data as any[], // not used in area, used in line graph
        xyPointData: item.data, // used in area
      };
    });
  }

  // TODO check for type improvement
  // eslint-disable-next-line
  protected drawAreaForSeries(lineWrapper: d3.Selection<SVGGElement, any, any, any>, dt: AdaptAreaGraphData, i: number): void {
    lineWrapper.append('path')
      .datum(dt.xyPointData)
      .classed('line-graph-transition line-chart-area', true)
      .attr('clip-path', `url(#${this._id}_clip)`)
      .attr('fill', this._colors[i])
      .attr('fill-opacity', this.areaOpacity)
      .attr('stroke', 'none')
      .attr('d', this.getAreaFilling())
      .on('mouseover', (data, ind, group) => {
        this.fillAreaDarkerColor(group[ind]);
      })
      .on('mouseout', (data, ind, group) => {
        this.fillAreaDefaultColor(group[ind]);
      });
  }

  protected onCircleMouseover(circleEl: SVGCircleElement, dt: AdaptAreaGraphData, d: AdaptLineGraphDateAndData): void {
    super.onCircleMouseover(circleEl, dt, d);
    this.fillAreaDarkerColor(
      d3.select(circleEl.closest('.line-chart-line-g')).select('.line-chart-area').node() as SVGPathElement
    );
  }

  protected onCircleMouseout(circleEl: SVGCircleElement): void {
    super.onCircleMouseout(circleEl);

    this.fillAreaDefaultColor(
      d3.select(circleEl.closest('.line-chart-line-g')).select('.line-chart-area').node() as SVGPathElement
    );
  }

  protected getCircleDarkenColor(baseColor: string, circleParentLineSelection: d3.Selection<d3.BaseType, {}, null, undefined>): string {
    return this._colorService.getDarken(circleParentLineSelection.attr('stroke'), .15, ColorType.HEX);
  }

  protected getDataForCirclesOrLines(dt: AdaptAreaGraphData): Point[] {
    return dt.xyPointData;
  }

  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  protected setCirclePosition(circleSel: d3.Selection<SVGCircleElement, any, SVGGElement, any>):
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
    d3.Selection<SVGCircleElement, any, SVGGElement, any> {
    return circleSel
      .attr('cx', (d) => this._xPos(d.x))
      .attr('cy', (d) => this._y(d.y));
  }

  protected updateLinesAreas(): void {
    this._g.selectAll<SVGPathElement, Point>('.line-chart-area').each((dat, i, g) => {
      d3.select(g[i]).attr('d', this.getAreaFilling());
    });
  }

  protected restrictXPosTicks(): void {
    /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
    const ticksCount = (this._x as any).ticks();

    if (this.showSlider && this.slidePointsCount) {
      this._x = d3.scaleLinear()
        .range([0, this._dim.w - (this._margin.left + this._margin.right)])
        /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
        .domain([ticksCount[0], ticksCount[this.slidePointsCount]]) as any;
    }
  }

  protected getTooltipData(baseColor: string, dt: AdaptAreaGraphData, d: Point): AdaptChartTooltip {
    return {
      color: baseColor,
      category: dt.name,
      xValue: d.x,
      yValue: this.scientificMode ? new BigNumber(d.y).toPrecision(2, 2) : d.y,
    };
  }

  protected getDataYValues(row: AdaptAreaGraphData): number[] {
    return row.xyPointData.map((item) => item.y);
  }

  private get _xPos(): d3.ScaleLinear<number, number> {
    /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
    return this._x as any as d3.ScaleLinear<number, number>;
  }

  private getAreaFilling(): d3.Area<Point> {
    return d3.area<Point>()
      .x((d) => this._xPos(d.x))
      .y0(this._y(0))
      .y1((d) => this._y(d.y));
  }

  private fillAreaDarkerColor(el: SVGPathElement): void {
    d3.select(el).attr('fill-opacity', this.areaOpacityForHovered);
  }

  private fillAreaDefaultColor(el: SVGPathElement): void {
    d3.select(el).attr('fill-opacity', this.areaOpacity);
  }
}
